body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color:lightblue;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.name{
  font-size:80pt;
}

img{
  height: 100pt;
  width: 100pt;
  margin-bottom: 50pt;
}

.footer img{
  margin-top: 170pt;
  height: 50pt;
  width: 50pt;
}

.nav{
  height: 100pt;

}
.input-size{
  width: 40%;
}

.make-neat{
  margin-left: 20pt;
  margin-right: 20pt;
}

.card-img{
  height:500px;
  width: 500px;
}
.card{
  height:400px;
  width: 400px;
}
.header-color{
  background-color:cornflowerblue;
}
a{
  text-decoration: none;
}
.a-tag {
  color: #ffffff;
  text-decoration: none;
  font-size: 40pt;
}

@media screen and (max-width:576px) {
  .name{
    font-size: 40pt;
  }
  .a-tag{
    font-size: 15pt;
  }
  .card-img{
    height: 300px;
    width: 300px;
  } 
  .input-size{
    width: 100%;
  }
}